.article1 {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.arrow-down {
  cursor: pointer;
  margin: 0 auto;
  margin-top: 5em;
}

.sub-title {
  font-weight: lighter !important;
}

.section2,
.section3,
.section4 {
  width: 100%;
  padding-top: 2em;
  padding-bottom: 2em;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.section2 > article > img,
.section3 > article > img,
.section4 > article > img {
  width: 100%;
  height: 400px;
  object-fit: cover;
  object-position: center;
  cursor: pointer;
}

.section2 .article3 .text {
  color: white;
  padding-bottom: 1.5em;
  line-height: 1.5em;
}

.section3 .article3 .text {
  color: white;
  padding-bottom: 1.5em;
  line-height: 1.5em;
}

.section2 .article3 .text2 {
  font-size: 10px;

  line-height: 1.5em;
}

.section3 .article3 .align-text {
  text-align: right;
}
.arrow {
  padding-top: 1.5em;
}

.article3 {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.btn {
  width: 100%;
  height: 50px;
  background: transparent;
  border: 1px solid white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  color: #fff;
  background-image: -webkit-linear-gradient(
    45deg,
    $amber-color-bg 50%,
    transparent 50%
  );
  background-image: linear-gradient(
    45deg,
    $amber-color-bg 50%,
    transparent 50%
  );
  background-position: 100%;
  background-size: 400%;
  -webkit-transition: background 450ms ease-out;
  transition: background 450ms ease-out;
}

.btn:hover {
  background-position: 0;
}

.copyright {
  width: 100%;
  height: 40px;
  background: $rich_black;
  text-align: center;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 1em;
  .copyright_text {
    margin: 0;
  }
}

@media only screen and (min-width: 320px) {
  section > article > .title {
    color: white;
    font-size: 30px;
    font-weight: bold;
    margin-top: 3em;
    padding-top: 3em;
    text-align: center;
  }

  .section2 .article3 .text,
  .section3 .article3 .text {
    margin: 10px;
  }

  .arrow-down {
    cursor: pointer;
    margin: 0 auto;
  }

  .article1 {
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 1em 1em 2em;
    display: flex;
  }

  .article4 {
    padding: 1em;
    display: grid !important;
    gap: 4em;
  }

  .section3 .article3 .align-text {
    text-align: right;
    font-size: 10px;
  }

  article.article2 {
    padding: 1em;
  }

  .btn {
    margin-bottom: 2em;
  }
}

@media only screen and (min-width: 680px) {
  body {
    margin: 1.4em 3em;
    overflow-x: hidden;
  }

  section > article > .title {
    font-size: 3em;
  }

  .btn {
    width: 500px;
  }

  .article3 {
    padding-bottom: 2em;
  }

  .article4 {
    display: grid !important;
    gap: 4em;
  }

  .img[data-scroll="in"] {
    transform: translateX(0px);
  }

  .section3 .article3 .align-text {
    text-align: right;
    font-size: 16px;
  }
}

.arrow {
  animation: linear infinite alternate;
  animation-name: move_the_arrow;
  animation-duration: 2s;
}
.same_arrow {
  animation: linear infinite alternate;
  animation-name: move_the_arrow;
  animation-duration: 2s;
}

@media only screen and (min-width: 920px) {
  .article4 {
    display: flex !important;
  }

  .section3 .article3 .align-text {
    text-align: right;
    font-size: 16px;
  }
}

@media only screen and (min-width: 1050px) {
  header {
    padding: 2em 2em 2em 4em;
  }

  .article4 {
    display: flex !important;
  }

  section > article > .title {
    color: white;
    font-size: 4em;
    font-weight: bold;
    margin-top: 0;
    text-align: justify;
  }

  .arrow-down {
    cursor: pointer;
    margin: 0 auto;
    margin-top: 8em;
  }

  .section3 .article3 .align-text {
    text-align: right;
    font-size: 16px;
  }
}

@media only screen and (min-width: 1250px) {
  header {
    padding: 2em 2em 2em 10em;
  }

  section > article > .title {
    color: white;
    font-size: 6em;
    font-weight: bold;
    text-align: justify;
    margin-top: 0;
    padding-top: 1em;
  }

  .section3 .article3 .align-text {
    text-align: right;
    font-size: 16px;
  }
  .arrow-down {
    cursor: pointer;
    margin: 0 auto;
    margin-top: 8em;
  }
}

@media only screen and (min-width: 1550px) {
  header {
    padding: 2em 2em 2em 20em;
  }

  section > article > .title {
    color: white;
    font-size: 6em;
    font-weight: bold;
    padding: 1em;
    text-align: justify;
  }

  .section3 .article3 .align-text {
    text-align: right;
    font-size: 16px;
  }

  .arrow-down {
    cursor: pointer;
    margin: 0 auto;
    margin-top: 5em;
  }
}

/*scroll out on text*/

.my-text[data-scroll] {
  transition: transform 2s ease-in-out;
}
.my-text[data-scroll="in"] {
  opacity: 1;
  transform: translateX(0);
}
.my-text[data-scroll="out"] {
  opacity: 0;
  transform: translateX(-300px);
}

/*scroll out on iamge*/

.img[data-scroll] {
  transition: transform 2s ease-in-out;
}

.img[data-scroll="in"] {
  animation: fadein 3s;
}

.img[data-scroll="out"] {
  opacity: 0;
}

.card-img[data-scroll] {
  transition: transform 3s ease-in-out;
}

.card-img[data-scroll="in"] {
  animation: fadein 2s;
}

.card-img[data-scroll="out"] {
  opacity: 0;
}

.card-img1[data-scroll] {
  transition: transform 3s ease-in-out;
}

.card-img1[data-scroll="in"] {
  animation: fadein 4s;
}

.card-img1[data-scroll="out"] {
  opacity: 0;
}

.card-img2[data-scroll="in"] {
  animation: fadein 6s;
}

.card-img2[data-scroll="out"] {
  opacity: 0;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes move_the_arrow {
  0% {
    left: 0;
    transform: translateX(0);
  }

  100% {
    left: 100%;
    transform: translateX(100%);
  }
}

@keyframes move_the_arrow2 {
  0% {
    left: 0;
    transform: translateX(100px);
  }

  100% {
    left: 100%;
    transform: translateX(-200%);
  }
}
